import React, { useState } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import People from "@material-ui/icons/People";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import facebookloginbutton from "../../assets/img/facebooklogin.png";
import googleSigninButton from "../../assets/img/googlesignin.png";
import logo from "../../assets/img/icon.png";
import facebookicon from "../../assets/img/facebookicon.png";
import googleicon from "../../assets/img/googleicon.png";

import styles from "assets/jss/material-kit-react/views/loginPage.js";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";

import image from "assets/img/back.jpg";
import { Paper, Button } from "@material-ui/core";
import { loginUser } from "services/user";
import { registerUser } from "services/user";

const useStyles = makeStyles(styles);

export default function LoginPage(props) {
  const [cardAnimaton, setCardAnimation] = React.useState("cardHidden");
  const [isLoginPage, setIsLoginPage] = useState(true);
  setTimeout(function() {
    setCardAnimation("");
  }, 700);
  const changePage = () => {
    setIsLoginPage(!isLoginPage);
  };
  const classes = useStyles();
  const responseFacebook = async response => {
    const {
      accessToken,
      userID,
      name,
      email = "",
      picture = { data: {} },
      graphDomain
    } = response;
    console.log(response);
    if (isLoginPage) {
      const loginReponse = await loginUser({ accessToken, userID });
      if (loginReponse.userId) {
      }
      console.log("loginReponse", loginReponse);
    } else {
      const request = {
        email,
        fbAccessToken: accessToken,
        picture: picture.data.url,
        userId: userID,
        userSource: graphDomain,
        username: name
      };
      const signupReponse = await registerUser(request);
      console.log("signupReponse", signupReponse);
    }
  };
  return (
    <div>
      <div
        className={classes.loginContainer}
        style={{
          backgroundImage: "url(" + image + ")"
        }}
      >
        <div style={{ flex: 1, marginTop: 50, zIndex: 1, paddingBottom: 50 }}>
          <GridContainer justify="center" style={{ margin: 0 }}>
            <GridItem xs={12} sm={12} md={4}>
              <Paper elevation={3} className={classes.loginPaper}>
                <div className={classes.logoContainer}>
                  <img src={logo} />
                  <h3>Mimir</h3>
                </div>
                <div className={classes.loginButton}>
                  <FacebookLogin
                    appId="218144749557220"
                    fields="name,email,picture"
                    callback={responseFacebook}
                    render={renderProps => (
                      <Button
                        onClick={renderProps.onClick}
                        variant="outlined"
                        color="primary"
                        className={classes.loginIconButton}
                      >
                        <img src={facebookicon} />
                        <span>Continue with Facebook</span>
                      </Button>
                    )}
                  />

                  <div className={classes.orLabel}>or</div>
                  <Button
                    variant="outlined"
                    color="secondary"
                    className={classes.loginIconButton}
                  >
                    <img src={googleicon} />
                    <span>Continue with Google</span>
                  </Button>
                </div>
                {isLoginPage ? (
                  <div className={classes.loginFooter}>
                    <h5>Don't have an account ?</h5>
                    <a onClick={changePage}>Create Account</a>
                  </div>
                ) : (
                  <div className={classes.loginFooter}>
                    <h6>By creating an account, you are agreeing to our</h6>
                    <h6 className={classes.termsLink}>
                      <span>{"Terms of Service "}</span> and
                      <span>{" Privacy Policy."}</span>
                    </h6>
                    <h5>Already have an account ?</h5>
                    <a onClick={changePage}>Login</a>
                  </div>
                )}
              </Paper>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    </div>
  );
}
